.DeckSelectionFadeOut{
  height: 80%;
  display: flex;
  justify-content: center; 
  transition: opacity 0.5s ease;
  opacity: 1;
}

.DeckSelectionFadeOut.fade-out{
  opacity: 0;
}

.DeckSelectionScreen{
    padding: 1rem;
    max-width: 1200px;
    opacity: 0;
    animation: fadeIn 2s ease forwards;
    height: 100%;
    width: 65%;
    font-family: 'Gloock';
}

@keyframes fadeIn {
    to {
      opacity: 1;
    }
  }

.DeckSelectionTitle{
  font-weight: 500;
  font-size: 2rem;
  margin-top: 0;
  margin-bottom: 0px;
  height: 8%;
}

.Footer{
  display: flex;
  justify-content: space-between;
  height: 5%;
}

.aircraft-grid {
    flex: 5;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
    width: 100%;
    height: 85%;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .PlayButton{
    opacity: 1;
    animation: opacity 0.5s ease forwards;
  }

  .PlayButton.Disabled{
    opacity: 0.5;
  }

.Third{
  width: 200px;
}

.DeckButton {
  font-family: 'Gloock';
}

@media (max-width: 800px) {
  .DeckSelectionFadeOut{
    width: 80%;
  }

  .DeckSelectionScreen{
    padding: 0rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .DeckSelectionTitle{
    font-size: 2rem;
    height: 8%;
  }

  .aircraft-grid{
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
    height: 50%;
  }

  .Footer{
    display: flex;
    flex-direction: column;
  }
}